$green: #9ccd3c;
$black: #191c25;
$white: #fafafb;
$OpenSans: 'Open Sans', sans-serif;
$Poppins: 'Poppins', sans-serif;
$Avenir: 'Maven Pro', sans-serif;

body {
  margin: 0;
  font-family: $OpenSans;
  font-size: 17px;
  line-height: 1.6;
  color: $black;
  font-weight: 400; }

.container {
    width: 100%;
    max-width: 1480px;
    margin: 0 auto; }


*, *:after, *:before {
    box-sizing: border-box; }

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100; }

.header_inner {
  display: flex;
  padding: 20px;
  justify-content: space-between;
  align-items: center; }

.header_logo {
  font-size: 40px;
  font-weight: 700;
  color: $white;
  font-size: 40px;
  text-transform: uppercase; }





.header_nav {
  font-size: 16px;
  text-transform: uppercase; }

.nav_item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 0 15px;
  transition: color .2s linear;
  max-width: 50px;
  &:hover {
    font-weight: 700;
    cursor: pointer; } }

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background: url("./img/fon.png") center no-repeat;
  background-size: cover; }


.intro_inner {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  z-index: 0; }

.intro_inf {
  width: 40%;
  position: absolute;
  background: rgba($black, 0.9);
  height: 100vh; }

.inf_text {
  max-width: 390px;
  font-size: 80px;
  color: $white;
  font-family: $Avenir;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 150px;
  line-height: 1.3;
  margin-bottom: 100px;
  transform: translateX(50%); }

.intro_btn {
  background-color: $green;
  font-size: 17px;
  color: $white;
  border: none;
  width: 260px;
  height: 60px;
  cursor: pointer;
  color: $white;
  font-family: $Avenir;
  text-transform: uppercase;
  font-weight: 700;
  transform: translateX(76%);
  &:hover {
    color: $black; } }

.about_btn {
  transform: none; }
.carusel {
   position: absolute;
   z-index: -1;
   right:  0;
   width: 70%;
   img {
    max-width: 1695px;
    height: 1000px; } }


.carusel_component {
  display:  flex;
  align-items:  center; }

.caruseil_items {
  height: auto;
  width: 100%;
  overflow:  hidden; }

.carusel_item {
  height: auto;
  display: flex;
  width: 100%;
  img {
    width: 1360px; } }

.carusel_arrow {
  cursor: pointer;
  font-size: 30px;
  height: 200px;
  width: 110px;
  text-align: center;
  padding-top: 80px;
  color: $white;
  background-color: $green;
  transition: color .2s linear; }

.arrow_left {
  position: absolute;
  margin-left: 190px; }



.arrow_right {
  margin-left: -110px; }

.arrow_right:hover, .arrow_left:hover {
  color: $black; }

.cards {
    margin-top: 150px; }

.cards_intro {
  display: flex;
  max-width: 1480px; }


.card {
  margin:  90px 0; }
.cards_inf {
  display: block;
  margin-bottom: auto;
  box-shadow: 0px 3px 28px #c7c7c74a;
  padding: 44px;
  width: 100%;
  max-width: 687px;
  height: 616px; }

.cards_title {
  font-family: $Avenir;
  font-size: 40px;
  font-weight: 700; }

.cards_text {
  max-width: 600px;
  font-size: 17px;
  font-family: $OpenSans;
  margin: 70px 0; }

.cards_social {
  display: flex;
  justify-content: space-between;
  color: $green;
  font-family: $Poppins;
  font-size: 15px; }

.social_item, .social_post {
  display: flex;
  align-items: center; }

.sosial_items {
  display: flex; }



.items_name:not(:last-child) {
  margin: 0px 15px 0px 35px; }

.social_post {
  margin-right:  10px; }

.social_item {
  i {
    font-size: 20px;
    cursor: pointer;
    transition: color .2s linear;
    &:hover {
      color: $black; } } }


.cards_image {
  display: flex;
  position: relative;
  margin-left: auto;
  margin-bottom: 30px; }

.image_inf {
  position: absolute;
  background-color: $green;
  top: 40%;
  width: 140px;
  height: 120px; }

.image_date {
  padding-top: 20px; }


.image_inf {
  color: $white;
  text-align: center;
  padding-top: 10px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: $OpenSans; }

.cards_img {
  height: 100%;
  height: 615px;
  width: 100%;
  max-width: 730px; }

.about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 684px;
    background: url("./img/4.png") center no-repeat;
    background-size: cover; }


.about_inner {
  color: $white;
  text-align: center; }

.about_title {
  font-size: $Avenir;
  font-weight: 700;
  font-size: 40px; }

.about_text {
  font-size: $OpenSans;
  font-size: 17px;
  width: 100%;
  max-width: 1140px;
  margin:  80px auto 70px auto; }


.information_inner {
  display: flex;
  max-width: 1480px;
  margin:  90px 0; }

.contact {
  width: 480px;
  margin-right: 40px; }

.contact_title, .testimonial_title {
  color: $black;
  font-size: $Avenir;
  font-weight: 700;
  font-size: 40px;
  margin-bottom: 20px; }

.testimonial_title {
  text-align: center; }


.message_input {
  padding-top: 12px;
  padding-left: 10px;
  padding-bottom: 5px;
  font-style: normal;
  height: 70px;
  line-height: 1.1;
  border-right: 0; }


.message_textarea {
  height: 200px;
  padding-top: 18px;
  padding-left: 10px;
  padding-bottom: 5px;
  resize: none; }

.message_input, .message_textarea {
  width: 90%;
  font-size: 17px;
  outline: none;
  background: none;
  border: none;
  box-shadow: 0px 4px 10px #c2c2c279;
  margin-bottom: 43px; }

::placeholder {
  color: #707070;
  font-size: 17px;
  font-family: $OpenSans; }


.massage_btn {
  cursor: pointer;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700;
  border: none;
  color: white;
  font-family: $OpenSans;
  padding: 20px 45px;
  margin-top: 10px;
  width: 190px;
  transition: color 0.3s linear;
  background: $green;
  &:hover {
    color: black; } }

.testimonial {
  box-shadow: 0px 3px 28px #c7c7c74a;
  padding: 44px;
  padding-left: 100px;
  padding-top: 50px;
  width: 100%;
  height: 750px;
  width: 900px; }


.slider_inf {
  display: flex; }

.slider_box {
    background: $green;
    padding: 13px;
    width: 190px;
    height: 100px;
    margin-top: -90px;
    margin-left: 15px; }

.slider_image {
  z-index: 2; }

.slider_name {
  margin-left: 45px;
  margin-top: 45px;
  line-height: 1.1; }

.slider_title {
  color: $black;
  font-size: 25px;
  font-family: $Avenir; }

.slider_subtitle {
  color: $green;
  font-size: 17px;
  font-family: $Avenir; }

.slider_text {
  font-family: $OpenSans;
  font-size: 17px;
  color: $black;
  width: 100%;
  max-width: 690px;
  margin-top: 70px; }



.slider {
  position: relative; }

.slider_arr {
  position: absolute;
  height: 200px;
  width: 100px;
  background-color: $green;
  color: $white;
  font-size: 40px;
  text-align: center;
  transition: color .2s linear;
  padding-top: 25px;
  right: 0;
  top: 30%;
  margin-right: -91px; }

.arr_left:hover, .arr_right:hover {
  cursor: pointer;
  color: $black; }

.arr_left {
  margin-bottom: 20px; }

.footer {
  background-color: $black;
  left: 0;
  right: 0;
  bottom: 0;
  color: $white;
  padding-bottom: 20px; }

.footer_inf, .footer_nav, .footer_socials {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  align-items: center;
  text-transform: uppercase; }

.footer_logo {
  font-weight: 700;
  font-family: $Avenir;
  font-size: 30px; }

.nav_link {
  margin-right: 55px;
  font-family: $OpenSans;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: color .2s linear; }

.nav_link:hover {
  color: $green;
  text-decoration: underline;
  text-underline-offset: 7px; }

.socials_item {
  margin-right: 20px;
  font-size: 30px;
  cursor: pointer;
  transition: color .2s linear;
  &:hover {
    color: $green; } }

.footer_underline:before {
  content: "";
  display: block;
  width: 100%;
  max-width: 1480px;
  height: 1px;
  background-color: $white;
  margin: 20px auto; }

.footer_underline {
  text-align: center; }

.underline_title {
  margin-top: 30px;
  text-transform: uppercase;
  font-family: $Avenir;
  font-weight: 700;
  font-size: 30px; }

.subscribe {
  margin: 20px 0;
  align-items: center; }

.email_input {
  width: 40%;
  font-size: 17px;
  outline: none;
  background: $white;
  color: $black;
  border: none;
  padding: 10px 30px;
  &:focus {
    outline: 0; } }

.email_btn {
  height: 40px;
  cursor: pointer;
  width: 60px;
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
  color: $white;
  background-color: $green;
  padding: 10px 20px;
  transition: color .2s linear;
  border: none;
  &:hover {
    color: black; }
  &:focus {
    outline: 0; } }


.underline_inf {
  display: flex;
  justify-content: space-between;
  width: 430px;
  margin: 0 auto; }

.inf_item {
  display: flex;
  align-items: center; }

.item_text {
  font-family: $Poppins;
  font-size: 17px;
  margin-left: 10px; }


.copiright {
  font-family: $OpenSans;
  font-weight: 700;
  color: #808287;
  font-size: 16px;
  margin-top: 60px; }


.top-to-btm {
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 60px;
  border: none;
  outline: none;
  color: $green;
  cursor: pointer;
  opacity: 0.5;
  &:hover {
    opacity: 1; } }





@media screen and ( max-width: 1400px ) {
  .intro_inf {
    width: 40%; }
  .inf_text {
    font-size: 60px;
    transform: translateX(30%);
    margin-bottom: 50px; }
  .intro_btn {
    transform: translateX(46%); }
  .carusel_item {
    img {
      width: 1000px; } }
  .carusel {
    img {
      max-width: 1060px;
      height: 675px; } }
  .arrow_left {
    position: absolute;
    margin-left: 140px; }
  .slider_text {
    max-width: 590px; }
  .testimonial {
    padding-left: 50px;
    width: 750px; }
  .contact {
    margin-left: 20px; }
  .about_btn {
    transform: translateX(0%); } }
